export default {
  install(Vue) {

    Vue.showErrorToast = ({ title, text }) => {
      Vue.notify({
        group: "main-notification-container",
        type: "error",
        title: title || "Error",
        text,
      });
    };

    Vue.showSuccessToast = ({ title, text }) => {
      Vue.notify({
        group: "main-notification-container",
        type: "success",
        title: title || "Success",
        text,
      });
    };

    // add log when loading components
    Vue.mixin({
      created() {
        //console.log("hello jack plugin: " + options);
      },
    });

    // add function to all components
    Vue.prototype.$bold = (text) => {
      return `<b>This is your input: ${text}</b>`;
    };

    // add global filter
    Vue.filter("localeDateString", (value) => {
      if (!(value instanceof Date)) {
        return value;
      }
      return value.toLocaleDateString();
    });

    // add custom directive
    Vue.directive("highlight", {
      inserted(el) {
        el.style.color = "red";
      },
    });
  },
};
