import axios from "axios";
import { messageService } from './services/messageService';
import config from "./myconfig.js";

axios.defaults.withCredentials = true;

process.env.NODE_TLS_REJECT_UNAUTHORIZED = '0';

var instance = axios.create({
  baseURL: config.baseURL,
  headers: {
    "Content-type": "application/json",
    //'Access-Control-Allow-Origin': 'http://localhost:8080'
  }
});

instance.interceptors.request.use((request) => {
  //console.log(request);
  return request;
}, error=> {
  console.log(error);
  Promise.reject(error);
});

instance.interceptors.response.use((response) => {
  console.log(response.status);
  return response;
}, (error) => {
  console.log("*** axios error occured ***");
  console.log(error);
  
  if (error.response) {
    console.log(error.response.status);
    if (error.response.status == 401 || error.response.status == 403) {
      console.log("Access is denied, redirecting to login page...");
      console.log(error.response.data);
      localStorage.clear();
      if (error.response.data.includes("Invalid session")) {
        //alert("Session timeout!");
        messageService.sendMessage('Session timeout!');
      }
      //window.location.href = "/";
    }
  }
  
  return Promise.reject(error);
});

export default instance;