import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);

export default new Router({
  mode: "history",
  routes: [
    {
      path: "/dashboard",
      alias: "/tutorials",
      name: "tutorials",
      component: () => import("./components/TutorialsList"),
    },
    {
      path: "/tutorials/:id",
      name: "tutorial-details",
      component: () => import("./components/Tutorial"),
    },
    {
      path: "/add",
      name: "add",
      component: () => import("./components/AddTutorial"),
    },
    {
      path: "/uploadfiles",
      name: "uploadfiles",
      component: () => import("./components/UploadFiles"),
    },
    {
      path: "/mytable",
      name: "mytable",
      component: () => import("./components/MyTable"),
    },
    {
      path: "/mychart",
      name: "mychart",
      component: () => import("./components/MyChart"),
    },
    {
      path: "/sudoku",
      name: "sudoku",
      component: () => import("./components/Sudoku"),
    },
    {
      path: "/scanQRCode",
      name: "scanQRCode",
      component: () => import("./components/ScanQRCode"),
    },
    {
      path: "/scanBarCode",
      name: "scanBarCode",
      component: () => import("./components/ScanBarCode"),
    },    
    {
      path: "/shoppingCart",
      name: "shoppingCart",
      component: () => import("./components/BarcodeCart"),
    },   
    {
      path: "/giftcardList",
      name: "giftcardList",
      component: () => import("./components/GiftCardList"),
    },       
    {
      path: "/stompTest",
      name: "stompTest",
      component: () => import("./components/StompTest"),
    },
    {
      path: "/myCanvas",
      name: "myCanvas",
      component: () => import("./components/MyCanvas"),
    },
    {
      path: "/myDrawingGame",
      name: "myDrawingGame",
      component: () => import("./components/MyDrawingGame"),
    },
    {
      path: "/",
      alias: "/login",
      name: "login",
      component: () => import("./components/Login"),
    },
/*   {
      path: "*",
      redirect: "/index.html",
    },
*/
  ],
});
