import Vue from "vue";
import Vuex from "vuex";
import VuePersist from "vuex-persist";
import moduleSudoku from "./store_sudoku";
import moduleBarcode from "./store_barcode";

Vue.use(Vuex);

const vuexPersist = new VuePersist({
  key: "my-app",
  storage: window.localStorage,
});

Vue.use(Vuex);

const moduleJack = {
  namespaced: false,
  state: {
    LoggedUser: "",
    LoggedUserRoles: "",
  },
  mutations: {
    loggedIn(state, userinfo) {
      state.LoggedUser = userinfo.split('|')[0];
      state.LoggedUserRoles = userinfo.split('|')[1];
    },
  },
  getters: {
    isAdmin: (state) => {
      let roles = state.LoggedUserRoles;
      return (roles && roles.includes("ROLE_ADMIN"));
    },
    currentUser: (state) => {
      return state.LoggedUser;
    }
  },
};

const store = new Vuex.Store({
  plugins: [vuexPersist.plugin],
  modules: {
    app: moduleJack, 
    sudoku: moduleSudoku,
    barcode: moduleBarcode
  }
});

//store.commit("loggedIn", "dummy|ROLE_DUMMY");
console.log(store.state.LoggedUserRoles);

export default store;
