import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import BootstrapVue from 'bootstrap-vue'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import * as util from "./util";
import store from "./store.js";
import { IconsPlugin } from 'bootstrap-vue'
import VueHotkey from 'v-hotkey'
import Ripple from 'vue-ripple-directive'
import ListDirective from "./directives/MyList.directive";
import Notifications from 'vue-notification';
import MyPlugin from './plugins/MyPlugin';
import VueCookies from 'vue-cookies';
//import Autoscroll from 'vue-autoscroll'
import VueChatScroll from 'vue-chat-scroll'

import VueIziToast from 'vue-izitoast';
import 'izitoast/dist/css/iziToast.css';
import vcolorpicker from 'vcolorpicker'

Vue.config.productionTip = false;

Vue.directive('ripple', Ripple);
Vue.directive("mylist", ListDirective);

Vue.use(BootstrapVue);
Vue.use(IconsPlugin);
Vue.use(VueHotkey);
Vue.use(Notifications);
Vue.use(MyPlugin);

Vue.use(VueIziToast);
Vue.use(VueCookies);
//Vue.use(Autoscroll);
Vue.use(VueChatScroll);
Vue.use(vcolorpicker)

router.beforeEach((to, from, next) => {
  if (to.name === "stompTest") next();
  else if (to.name !== 'login' && !util.isAuthenticated()) next({ name: 'login' });
  else if (to.name === 'login' && util.isAuthenticated()) next({ name: 'dashboard' });
  else next();
});
util.isAuthenticated();

new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount("#app");
