<template>
  <div id="app" style="height: 100%;">
    <nav class="navbar navbar-expand navbar-dark bg-dark">
      <router-link to="/dashboard" class="navbar-brand">JackTest</router-link>
      <div class="navbar-nav mr-auto">
        <b-nav-item-dropdown text="" right>
          <b-dropdown-item to="/tutorials">Tutorials</b-dropdown-item>
          <b-dropdown-item to="/add">Add</b-dropdown-item>
          <b-dropdown-item to="/uploadfiles">Upload</b-dropdown-item>
          <b-dropdown-item to="/mytable">My Table</b-dropdown-item>
          <b-dropdown-item to="/mychart">My Chart</b-dropdown-item>
          <b-dropdown-divider></b-dropdown-divider>
          <b-dropdown-item to="/sudoku">Sudoku</b-dropdown-item>
          <b-dropdown-item to="/scanQRCode"
            ><b-icon icon="upc-scan"> </b-icon> Scan</b-dropdown-item
          >
          <b-dropdown-item to="/stompTest">Stomp Chat</b-dropdown-item>
          <b-dropdown-item to="/myCanvas">My Canvas</b-dropdown-item>
          <b-dropdown-item to="/myDrawingGame">My Drawing Game</b-dropdown-item>
          <b-dropdown-divider></b-dropdown-divider>
          <b-dropdown-item to="/scanBarcode"><b-icon icon="upc-scan"/>Scan Barcode</b-dropdown-item>
          <b-dropdown-item to="/shoppingCart">Shopping Cart</b-dropdown-item>
          <b-dropdown-divider></b-dropdown-divider>
          <b-dropdown-item to="#"
            ><span @click="logout()">Log out</span></b-dropdown-item
          >
        </b-nav-item-dropdown>

        <li class="nav-item">
          <router-link to="/scanQRCode" class="nav-link"><b-icon icon="upc-scan" /> QR Login</router-link>
        </li>
        <li class="nav-item">
          <router-link to="/sudoku" class="nav-link" v-if="isAdmin">Sudoku</router-link>
        </li>
        <li class="nav-item">
          <router-link to="/myDrawingGame" class="nav-link">Drawing Game</router-link>
        </li>
        <b-nav-item-dropdown text="Shopping">
          <b-dropdown-item to="/scanBarcode"><b-icon icon="upc-scan"/> Barcode</b-dropdown-item>
          <b-dropdown-item to="/shoppingCart">Shopping Cart</b-dropdown-item>
          <b-dropdown-divider></b-dropdown-divider>
          <b-dropdown-item to="/giftCardList" v-if="isAdmin"><b-icon icon="credit-card" /> Gift Card</b-dropdown-item>
        </b-nav-item-dropdown>        
      </div>
      <b-nav-text> Welcome {{ currentUser }} </b-nav-text>
      <div class="navbar-nav ml-auto">
        <li class="nav-item">
          <b-button
            v-ripple
            pill
            variant="success"
            v-b-tooltip.hover
            title="Log out from my tutorial"
            @click="logout()"
          >
            <b-icon icon="power"></b-icon>Logout</b-button
          >
        </li>
      </div>
    </nav>

    <notifications group="main-notification-container" position="top center" />

    <div
      class="container mt-3"
      v-show="show"
      v-hotkey="{ esc: onHide, 'ctrl+enter': onShow }"
    >
      <router-view />
    </div>
  </div>
</template>

<script>
import TutorialDataService from "./services/TutorialDataService";
import Vuex from "vuex";

import { messageService } from "./services/messageService";

export default {
  name: "app",

  data() {
    return {
      show: true,
      subscription: null,
    };
  },

  computed: {
    ...Vuex.mapGetters(["isAdmin", "currentUser"]),
  },

  methods: {
    onHide() {
      this.show = false;
    },
    onShow() {
      this.show = true;
    },

    logout() {
      TutorialDataService.logout()
        .then((resp) => {
          console.log(resp.data);
          localStorage.clear();
          console.log("logged out successfully");
          //this.$router.push('/')

          // Note: logout POST request return 302 to login?logout
          // and Browser doesn't redirect 302 for POST request
          // hence below will manually redirect

          // # redirect to spring default logout page
          //window.location.href = "login?logout";

          // # redirect to mylogin.html?logout or root / (will be login page)
          window.location.href = "/";
        })
        .catch((e) => {
          console.log(e);
        });
    },
  },

  created() {
    // subscribe to home component messages
    this.subscription = messageService
      .getMessage()
      .subscribe(async (message) => {
        if (message) {
          console.log("##: " + message.text);
          this.$toast.warning(message.text, "", {
            position: "center",
            overlay: true,
            close: false,
          });

          await new Promise((r) => setTimeout(r, 5000));

          window.location.href = "/";
        }
      });
  },
  beforeDestroy() {
    // unsubscribe to ensure no memory leaks
    this.subscription.unsubscribe();
  },
};
</script>

<style>
  .container {
    height: 100%;
    overflow-y: auto;
  }
</style>
