import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

const moduleBarcode = {
  namespaced: true,
  state: {
    itemList: [],
    started: false,
    paymentStarted: false,
    paymentInfo: {},
    transactionCompleted: false,
  },
  mutations: {
    updateData(state, data) {
      console.log('original data: ', data);

      state.started = data.started;
      state.paymentStarted = data.paymentStarted;
      state.itemList = data.allItems;
      state.paymentInfo = data.paymentInfo;
      state.transactionCompleted = data.transactionCompleted;
    },
  },
  actions: {
    update({ commit }, data) {
      commit('updateData', data);
    },
  }
};

export default moduleBarcode;