function isAuthenticated() {
  if (localStorage.getItem("LoggedUser")) return true;
  return false;
}

// note: localStorage is not reactive, which means changes in localStorage doesn't trigger
// re-evaluating the code
// Solution - Switch to user Vuex + localStorage (see store.js)
function isAdmin() {
  let roles = localStorage.getItem("LoggedUserRoles");
  if (roles && roles.includes("ROLE_ADMIN")) return true;
  return false;
}

export {isAuthenticated, isAdmin};