import Vue from 'vue'
import Vuex from 'vuex'

import sudokuModule from './services/SudokuService';

Vue.use(Vuex);

/*
difficulty: easy,medium,hard,very-hard,insane,inhuman
*/

// Define difficulties by how many squares are given to the player in a new
// puzzle.
var DIFFICULTY = {
  "easy": 62,
  "medium": 53,
  "hard": 44,
  "very-hard": 35,
  "insane": 26,
  "inhuman": 17,
};

const moduleSudoku = {
  namespaced: true,
  state: {
    difficulties: ["easy", "medium", "hard", "very-hard", "insane", "inhuman"],
    grid: null,
    selected: null,
    wonGame: false,

    timeInSeconds: 0,
    timerID: NaN
  },
  mutations: {
    initGrid(state, data) {
      state.wonGame = false;
      console.log('original data: ', data);

      state.grid = sudokuModule.board_string_to_grid(data.puzzle);

      let solvedGrid = sudokuModule.board_string_to_grid(data.solved);

      // change . to "", also store a ob instead of just numbers
      for (let i = 0; i < state.grid.length; i++) {
        for (let x = 0; x < state.grid[i].length; x++) {

          let newVal = {
            value: parseInt(state.grid[i][x], 10),
            locked: true,
            selected: false,
            solution: parseInt(solvedGrid[i][x], 10),
            error: false
          };
          if (state.grid[i][x] === '.') {
            newVal.value = '';
            newVal.locked = false;
          }
          state.grid[i][x] = newVal;
        }
      }
    },
    setNumber(state, x) {
      if (!state.selected) return;
      let row = state.grid[state.selected.x];
      row[state.selected.y].value = x;

      // highlight incorrect answers
      if (x !== state.grid[state.selected.x][state.selected.y].solution) {
        row[state.selected.y].error = true;
      } else {
        row[state.selected.y].error = false;
      }
      console.log(state.grid[state.selected.x][state.selected.y].solution);

      // clear error
      if (x === '') {
        row[state.selected.y].error = false;
      }
      
      Vue.set(state.grid, state.selected.x, row);
      /*
      did we win? this feels like it should be it's own method
      */
      let won = true;
      for (let i = 0; i < state.grid.length; i++) {
        for (let x = 0; x < state.grid[i].length; x++) {
          if (state.grid[i][x].value !== state.grid[i][x].solution) won = false;
        }
      }
      if (won) {
        state.wonGame = true;
        clearInterval(state.timerID);  // stop timer
      }
    },
    setSelected(state, pos) {
      if (state.grid[pos.x][pos.y].locked) return;
      for (let i = 0; i < state.grid.length; i++) {
        let row = state.grid[i];
        for (let x = 0; x < row.length; x++) {
          if ((i !== pos.x || x !== pos.y) && row[x].selected) {
            row[x].selected = false;
          }
          if (i === pos.x && x === pos.y) {
            row[x].selected = true;
            state.selected = pos;
          }
        }
        Vue.set(state.grid, i, row);
      }
    },
    increaseTimer(state) {
      state.timeInSeconds++;
    }
  },
  actions: {
    generatePuzzle({ commit, state }, difficulty) {
      if (!difficulty) difficulty = state.difficulties[0];

      console.log("generating puzzle for " + difficulty);

      sudokuModule.generate(DIFFICULTY[difficulty])
        .then(response => {
          commit('initGrid', response.data)
        })
    },
    startGame({ commit, state }) {
      clearInterval(state.timerID);
      state.timeInSeconds = 0;
      state.timerID = setInterval(() => {
        commit("increaseTimer");
      }, 1000)
    },
  }
};

export default moduleSudoku;